<template>
    <div style="width:90%">
        <div class="mb-6">
            <h2>Informacion del Usuario</h2>
        </div>
        <div class="mt-6">
            <v-card>
                <v-card-title>
                    Nombre: {{datos.nombre}}
                </v-card-title>
                <v-card-title>
                    Apellido: {{datos.apellido}}
                </v-card-title>
                <v-card-title>
                    Correo: {{ datos.correo }}
                </v-card-title>
                <v-card-title>
                    Telefono: {{ datos.telefono }}
                </v-card-title>
            </v-card>
        </div>
        <div class="d-flex justify-end mt-6">
            <v-btn @click="openDialog">
                <v-icon>mdi-pencil</v-icon>
                <span>Modificar</span>
            </v-btn>
        </div>
        <div>
            <v-dialog 
            v-model="dialog">
                <v-card class="pa-4">
                    <v-form class="pa-4">
                        <v-row>
                            <v-text-field
                            label="Nombre"
                            v-model="profileTemplate.nombre">
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Apellido"
                            v-model="profileTemplate.apellido">
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Correo"
                            v-model="profileTemplate.correo">    
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Telefono"
                            v-model="profileTemplate.telefono">    
                            </v-text-field>
                        </v-row>
                    </v-form>
                    <div>
                        <v-row class="pa-4">
                            <v-btn @click="cerrarDialog" >
                                <v-icon>mdi-close-box</v-icon>
                                Cancelar
                            </v-btn>
                            <v-spacer></v-spacer>
                            <v-btn @click="accionGuardar" >
                                <v-icon>mdi-content-save-edit</v-icon>
                                Guardar
                            </v-btn>
                        </v-row>
                    </div>
                </v-card>
            </v-dialog>
        </div>
    </div>
</template>

<script>

  export default {
    data(){
        return {
            datos: this.$store.getters.profileInfo,
            dialog: false,
            profileTemplate: {
                nombre: '',
                apellido: '',
                correo: '',
                telefono: ''
            }
        }
    },
    methods:{   
        openDialog(){
            this.profileTemplate = this.datos,
            this.dialog = true
        },
        cerrarDialog(){
            this.dialog = false,
            this.profileTemplate ={
                nombre: '',
                apellido: '',
                correo: '',
                telefono: ''
            }
        },
        accionGuardar(){
            this.$store.commit('guardarInfoPerfil', {
                nombre: this.profileTemplate.nombre,
                apellido: this.profileTemplate.apellido,
                correo: this.profileTemplate.correo,
                telefono: this.profileTemplate.telefono
            })
            this.cerrarDialog()
        }
    }
  }
</script>